<template>
<div class="account">
  <div class="section">
    <div class="content">
      <div class="account-details">
        <h1>Welcome {{user.username}}!</h1>
        <button class="button-primary" @click="submitLogout">Logout</button>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import {
  mapState,
  mapActions
} from 'vuex';

export default {
  name: 'Account',
  data() {
    return {}
  },
  methods: {
    submitLogout() {
      this.logout();
      this.$router.push({
        path: '/'
      });
    },
    ...mapActions(['logout'])
  },
  computed: {
    ...mapState(['user'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



